/* wp-block-media-text */
@layer components {
	.wp-block-media-text {
		@apply my-12 lg:my-24;

		.wp-block-media-text__media {
			@apply mb-6 lg:mb-0 !important;
		}
		.wp-block-media-text__content {
			@apply px-0 sm:px-12 space-y-3 !important;
			a {
				@apply button;
			}
		}
		&.has-background {
			@apply rounded-theme;
			.wp-block-media-text__content {
				@apply px-6 pb-6 sm:px-12 !important;
			}
		}
	}
}
